import React, { createRef, Fragment, useEffect, useReducer, useRef, useState } from "react";
import InputRange from "react-input-range";
const gifFrames = require("gif-frames");

export function GifApp({ gifUrl }) {
  const [frames, setFrames] = useState([]);
  const rawFrames = useRef([]);
  const total = useRef(1);

  useEffect(() => {
    (async () => {
      const rawFrames1 = await gifFrames({ url: gifUrl, frames: "all", outputType: "canvas", cumulative: true });
      total.current = rawFrames1.length;
      rawFrames.current = rawFrames1;
      dispatch({ type: "UPDATE" });
    })();
  }, []);

  let calcCountReducer = (state, action) => {
    switch (action.type) {
      case "UPDATE":
        return { ...state, calcCount: state.calcCount + 1 };
      default:
        throw new Error("Invalid Action Type");
    }
  };
  let [state, dispatch] = useReducer(calcCountReducer, { calcCount: 0 });

  useEffect(() => {
    if (!rawFrames.current[0]) {
      return;
    }
    setFrames((prev) => {
      if (!rawFrames.current[0]) {
        return prev;
      }

      return prev.concat([rawFrames.current[0].getImage()]);
    });
    rawFrames.current.shift();
    setTimeout(() => {
      dispatch({ type: "UPDATE" });
    }, 0);
  }, [state.calcCount]);

  if (state.calcCount < total.current) {
    return (
      <div className="text-center p-4 border bg-light">
        <div className="d-flex justify-content-center">
          <div className="spinner-border text-danger mr-2" role="status">
            <span className="sr-only">Loading...</span>
          </div>
          <span className="mt-1">({Math.floor((state.calcCount / total.current) * 100)}%)</span>
        </div>
      </div>
    );
  }

  return <Renderer frames={frames} />;
}

function Renderer({ frames }) {
  const canvas = createRef();
  const [state, setState] = useState(0);
  const parentRef = useRef();

  useEffect(() => {
    if (!canvas.current) {
      return;
    }

    const width = parentRef.current.offsetWidth;

    const img = frames[0];
    const scale = width / img.width < 1 ? width / img.width : 1;
    canvas.current.width = img.width * scale;
    canvas.current.height = img.height * scale;
    canvas.current.getContext("2d").scale(scale, scale);
    canvas.current.getContext("2d").drawImage(img, 0, 0);
  }, [canvas]);

  useEffect(() => {
    if (frames[state] === undefined) {
      return;
    }
    const img = frames[state];
    const width = parentRef.current.offsetWidth;
    const scale = width / img.width < 1 ? width / img.width : 1;
    canvas.current.width = img.width * scale;
    canvas.current.height = img.height * scale;
    canvas.current.getContext("2d").scale(scale, scale);
    canvas.current.getContext("2d").drawImage(img, 0, 0);
  }, [state]);

  return frames.length ? (
    <div className="text-center" ref={parentRef}>
      <canvas className="text-center m-auto" ref={canvas} />
      <InputRange formatLabel={() => ""} minValue={0} maxValue={frames.length - 1} value={state} onChange={setState} />
    </div>
  ) : (
    ""
  );
}
